export function dialogsPageReducer(state = initialState, action){
    let newState = {...state};
    switch (action.type) {
                case ADD_MESSAG:{
                    if(action.mes !== ''){
                        let mess = {messag:action.mes, id: newState.messag.length+1};
                        newState.messag.push(mess);
                    }
                    break;
                }
                default: {}
            }
            return newState
}